<template>
  <div>
    <el-row>
      <el-page-header @back="goBack" content="历史数据"> </el-page-header>
    </el-row>
    <el-row class="top" type="flex" justify="start">
      <el-col :span="1">
        <el-checkbox label="全选" name="type"></el-checkbox>
      </el-col>
      <el-col :span="2" v-for="dev in devices" :key="dev.deviceCode">
        <el-checkbox :label="dev.deviceCode" name="type"></el-checkbox>
      </el-col>
    </el-row>
    <el-row class="top" type="flex" justify="start">
      <el-col :span="24">
        <el-form
          :inline="true"
          :model="deviceSearchForm"
          class="align-left"
        >
          <el-form-item label="查询时间">
            <el-date-picker
              v-model="searchTimeBetween"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onDeviceSearchSubmit"
              >查询</el-button
            >
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onDeviceSearchSubmit"
              >导出</el-button
            >
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
          <el-table :data="tableData" stripe style="width: 100%">
            <el-table-column prop="number" label="序号" width="180"></el-table-column>
            <el-table-column prop="time" label="时间" width="180">
            </el-table-column>
            <el-table-column prop="deviceCode" label="设备编号" width="180">
            </el-table-column>
            <el-table-column prop="tag" label="卡号"> </el-table-column>
          </el-table>
      </el-col>
    </el-row>
    <el-row type="flex" class="row-bg device-list-pagination" justify="end">
      <el-pagination background layout="prev, pager, next" :total="1000">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "HistoryData",
  data() {
    return {
      searchTimeBetween: null,
      devices: [
        {
          deviceCode: "000001",
        },
        {
          deviceCode: "000002",
        },
        {
          deviceCode: "000003",
        },
        {
          deviceCode: "000004",
        },
      ],
      deviceSearchForm: {
        deviceState: null,
        deviceCode: null,
        deviceType: "pos",
        deviceIp: null,
      },
      tableData: [
        {
          number: 1,
          time: "2016-05-02",
          deviceCode: "王小虎",
          tag: "上海市普陀区金沙江路 1518 弄",
        },
        {
          number: 2,
          time: "2016-05-04",
          deviceCode: "王小虎",
          tag: "上海市普陀区金沙江路 1517 弄",
        },
        {
          number: 3,
          time: "2016-05-01",
          deviceCode: "王小虎",
          tag: "上海市普陀区金沙江路 1519 弄",
        },
        {
          number: 4,
          time: "2016-05-03",
          deviceCode: "王小虎",
          tag: "上海市普陀区金沙江路 1516 弄",
        },
      ],
    };
  },
  methods: {
    onDeviceSearchSubmit() {},
    goBack() {},
  },
};
</script>
<style>
.align-left {
  text-align: left;
}
.top {
  margin-top: 20px;
}
.device-list-charts {
  text-align: left;
  margin-top: 20px;
}
.device-list-pagination {
  margin-top: 30px;
}
</style>



