<template>
  <div>
    <el-row>
      <el-page-header @back="goBack" content="实时数据"> </el-page-header>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form :inline="true" class="device-list-charts">
          <el-form-item label="当前活动数量">
            <el-progress type="circle" :percentage="70"></el-progress>
          </el-form-item>
          <el-form-item label="当前离线设备总数">
            <el-progress
              type="circle"
              :percentage="50"
              status="exception"
            ></el-progress>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row type="flex" justify="end">
      <el-col :span="2">
        <el-button type="primary" icon="el-icon-search">导出</el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col class="box-card" :span="4" v-for="dev in deviceMap" :key="dev[0]">
        <el-card>
          <div slot="header" class="clearfix">
            <el-checkbox v-model="dev[1].checked"></el-checkbox>
            <span>设备:{{dev[0]}}</span>
          </div>
          <div v-for="tag in dev[1].datas" :key="tag" class="text item">
            {{ tag }}
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "RealtimeData",
  data() {
    return {
      deviceMap:new Map(),
    };
  },
  methods: {
    goBack() {},
  },
  created(){
    for(let i=1;i<50;i++){
      this.deviceMap.set('10000A'+i,{checked:false,datas:['00000001','00000002','00000003']});
    }
  }
};
</script>
<style>
.device-list-charts {
  text-align: left;
  margin-top: 30px;
}
.box-card {
  padding: 5px;
}
</style>



