<template>
  <div>
    <el-row>
      <el-page-header @back="goBack" content="设备列表"> </el-page-header>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form :inline="true" class="device-list-charts">
          <el-form-item label="当前活动数量">
            <el-progress type="circle" :percentage="70"></el-progress>
          </el-form-item>
          <el-form-item label="当前离线设备总数">
            <el-progress
              type="circle"
              :percentage="50"
              status="exception"
            ></el-progress>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form
          :inline="true"
          :model="deviceSearchForm"
          class="demo-form-inline"
        >
          <el-form-item label="状态">
            <el-select
              v-model="deviceSearchForm.deviceState"
              placeholder="状态"
            >
              <el-option label="在线" value="online"></el-option>
              <el-option label="离线" value="offline"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="设备编号">
            <el-input
              v-model="deviceSearchForm.deviceCode"
              placeholder="设备编号"
            ></el-input>
          </el-form-item>
          <el-form-item label="IP地址">
            <el-input
              v-model="deviceSearchForm.deviceIp"
              placeholder="IP地址"
            ></el-input>
          </el-form-item>
          <el-form-item label="设备类型">
            <el-select
              v-model="deviceSearchForm.deviceType"
              placeholder="设备类型"
            >
              <el-option label="人员定位" value="pos-dev"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onDeviceSearchSubmit"
              >查询</el-button
            >
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <template>
          <el-table :data="tableData" stripe style="width: 100%">
            <el-table-column prop="date" label="日期" width="180">
            </el-table-column>
            <el-table-column prop="name" label="姓名" width="180">
            </el-table-column>
            <el-table-column prop="address" label="地址"> </el-table-column>
          </el-table>
        </template>
      </el-col>
    </el-row>
    <el-row type="flex" class="row-bg device-list-pagination" justify="end">
      <el-pagination background layout="prev, pager, next" :total="1000">
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "DeviceList",
  data() {
    return {
      deviceSearchForm: {
        deviceState: null,
        deviceCode: null,
        deviceType: "pos",
        deviceIp: null,
      },
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄",
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄",
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄",
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄",
        },
      ],
    };
  },
  methods: {
    onDeviceSearchSubmit() {},
    goBack() {},
  },
};
</script>
<style>
.device-list-charts {
  text-align: left;
  margin-top: 20px;
}
.device-list-pagination{
  margin-top: 30px;
}
</style>



